.fifth-page {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 400%;
    background: linear-gradient(-180deg, rgb(23, 26, 31), rgb(20, 23, 28));
}

.fifth-container {
    position: absolute;
    right: 14%;
    top: 32%;
    width: 70%;
    font-size: large;
    padding: 20px;
    min-height: 300px;
    max-height: 300px;
    overflow: hidden;
}

.fifth-container ul {
    list-style-type: none;
    color: black;
    display: inline-flex;
    justify-content: center;
    overflow: hidden;
    text-align: center;
}

.fifth-container ul>li {
    display: block;
    background-color: white;
    min-width: 200px;
    min-height: 250px;
    margin-left: 50px;
    border-radius: 10px;
}

.fifth-container ul>li>img {
    width: 180px;
    height: 180px;
    margin-top: 10px;
    border-radius: 10px;
    margin-bottom: 15px;
}

@keyframes slide {
    0% {
        transform: translateX(0%);
    }

    100% {
        transform: translateX(-60%);
    }
}

.ul-container ul {
    overflow: hidden;
    scroll-behavior: smooth;
    position: relative;
    animation-name: slide;
    animation-duration: 50s;
    animation-timing-function: ease;
    animation-iteration-count: infinite;
    animation-play-state: paused;
}