.third-container {
    position: absolute;
    right: 14%;
    top: 32%;
    width: 70%;
    font-size: large;
    padding: 20px;
    user-select: none;
}

.third-page {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 200%;
    background: linear-gradient(-180deg, rgb(34, 40, 47), rgb(29, 34, 40));
}

.third-page p {
    color: white;
    font-family: 'Manrope', sans-serif;
}

.third-container ul {
    list-style-type: disc;
    position: relative;
    left: 30px;
}

.third-container li {
    margin: 0;
    padding: 0;
    color: white;
    list-style: none;
    display: list-item;
    margin-left: 10px;
    margin-bottom: 100px;
}

.third-container li:first-child {
    margin-top: 10px;
    margin-bottom: 100px;
}

.third-container li:last-child {
    margin-top: 10px;
    margin-bottom: 10px;
}

.third-container .slider {
    position: absolute;
    width: 12px;
    height: 100%;
    background: #50d890;
    border-radius: 20px;
    top: 0;
}

.third-container .slider .inner-slider {
    margin: auto;
    margin-top: 12px;
    width: 4px;
    height: 75px;
    background: black;
    border-radius: 20px;
    transition: all 75ms ease-in-out;
}

.third-container .inner-div {
    float: right;
    margin-right: 10px;
}

.third-container .inner-div ul {
    width: 140px;
}

.third-container .inner-div ul>li {
    font-weight: 200;
    opacity: 0.4;
}

.third-container .inner-div .selected-year {
    font-weight: 600;
    opacity: 1;
}

.third-container .education-text {
    padding: 20px;
}

.third-container .education-text p {
    text-align: center;
    color: white;
    font-weight: 600;
    margin-top: 120px;
    letter-spacing: 2px;
}

#year2014,
#year2017,
#year2020 {
    cursor: pointer;
}