.keyword {
    color: rgb(204, 120, 50)
}

.variable {
    color: rgb(169, 183, 198);
}

.string {
    color: rgb(106, 135, 89);
}

.array-index {
    color: rgb(104, 151, 187);
}

.output-string {
    color: rgba(255, 255, 255, 0.6);
}

.second-page {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 100%;
    background: linear-gradient(-180deg, rgb(40, 47, 55), rgb(34, 40, 47));
}

.second-container {
    position: absolute;
    right: 14%;
    top: 32%;
    width: 70%;
    font-size: large;
    padding: 20px;
}

.second-container .title-bar {
    width: 100%;
    height: 40px;
    background-color: rgb(53, 56, 64);
    border-radius: 20px 20px 0 0;
}

.second-container .title-bar>ul>li {
    list-style-type: none;
    display: inline-block;
    width: 12px;
    height: 12px;
    margin-top: 15px;
    margin-left: 10px;
    border-radius: 100%;
}

.second-container .title-bar>ul>li:nth-child(1) {
    background-color: rgb(255, 95, 87);
    margin-left: 22px;
}

.second-container .title-bar>ul>li:nth-child(2) {
    background-color: rgb(254, 188, 46);
}

.second-container .title-bar>ul>li:nth-child(3) {
    background-color: rgb(40, 200, 64);
}

.second-container .details-window {
    width: 100%;
    height: 400px;
    display: block;
    background-color: rgb(40, 44, 52);
    ;
    border-radius: 0 0 20px 20px;
}

.second-container .details-window>#programming>p {
    font-size: medium;
    font-family: 'Courier New', Courier, monospace;
    font-weight: 600;
}

.second-container .details-window>#output-log>p {
    font-size: large;
    font-family: 'Manrope', sans-serif;
    font-weight: 600;
    color: white;
}

.second-container .run-button {
    width: 50px;
    height: 50px;
    position: absolute;
    right: 0;
    transform: translate(-50px, -75px);
    background-color: rgba(0, 255, 187, 0.5);
    border-radius: 100%;
    animation-name: ripple;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    cursor: pointer;
}

.second-container .run-button>* {
    width: 50px;
    height: 50px;
}