@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@300&display=swap");

* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth !important;
  overscroll-behavior-y: none;
}

@keyframes slide-info {
  0% {
    transform: translateY(-72px);
  }

  25% {
    transform: translateY(-48px);
  }

  50% {
    transform: translateY(-24px);
  }

  100% {
    transform: translateY(0px);
  }
}

.nav-bar {
  position: fixed;
  top: 20px;
  margin: 0;
  width: 100%;
  padding-top: 20px;
  padding-bottom: 20px;
  backdrop-filter: blur(10px);
  z-index: 1;
}

.nav-bar ul {
  list-style: none;
  text-align: center;
}

.nav-bar li {
  display: inline;
  margin: 40px;
}

.nav-bar ul>li>a {
  text-decoration: none;
  color: white;
}

.nav-bar ul>li>a:hover {
  color: rgb(0, 255, 187);
}

.selected-page {
  color: rgb(0, 255, 187);
  text-underline-position: below;
  text-underline-offset: 10px;
  text-decoration: underline;
  text-decoration-thickness: 2px;
}

.page-title {
  color: white;
  font-family: 'Manrope', sans-serif;
  position: absolute;
  top: 20%;
  right: 14%;
}

#programming>p {
  white-space: pre-wrap;
}

@media screen and (max-width: 1145px) {
  .nav-bar {
    position: fixed;
    top: 0px;
    margin: 0;
    width: 100%;
    background-color: blue;
    display: none;
    /* padding-top: 20px;
    padding-bottom: 20px; */
    /* backdrop-filter: blur(0px) !important; */
    z-index: 1;
  }

  .nav-bar ul {
    list-style: none;
    text-align: center;
  }

  .nav-bar li {
    display: inline;
    margin: 10px;
  }

  .nav-bar ul>li>a {
    text-decoration: none;
    color: white;
  }

  .first-page {
    background: black;
    font-size: 2px;
  }

  .first-decoration {
    animation: none;
    will-change: unset;
    border-radius: 0;
  }

  .first-page-decoration {
    display: none;
    width: 0;
    height: 0;
  }

  .profile-picture-holder {
    top: 0;
    border: none;
    border-radius: 0;
    padding: 0;
    backdrop-filter: none;
    margin-top: 50px;
  }

  .profile-picture-holder h4 {
    position: revert;
    font-weight: revert;
  }
}