.fourth-page {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 300%;
    background: linear-gradient(-180deg, rgb(29, 34, 40), rgb(23, 26, 31));
}


.fourth-container {
    position: absolute;
    right: 14%;
    top: 32%;
    width: 70%;
    font-size: large;
    padding: 20px;
    min-height: 300px;
    user-select: none;
}

.fourth-page p {
    color: white;
    font-family: 'Manrope', sans-serif;
}

.fourth-container ul {
    list-style-type: disc;
    position: relative;
    left: 30px;
    top: 110px;
}

.fourth-container li {
    margin: 0;
    padding: 0;
    color: white;
    list-style: none;
    display: list-item;
    margin-left: 10px;
    margin-bottom: 100px;
    cursor:pointer;
}

.fourth-container li:first-child {
    margin-top: 10px;
    margin-bottom: 100px;
}

.fourth-container li:last-child {
    margin-top: 10px;
    margin-bottom: 10px;
}

.fourth-container .slider {
    position: absolute;
    width: 12px;
    height: 65%;
    background: #50d890;
    border-radius: 20px;
    top: 120px;
}

.fourth-container .slider .inner-slider {
    margin: auto;
    margin-top: 12px;
    width: 4px;
    height: 45px;
    background: black;
    border-radius: 20px;
    transition: all 75ms ease-in-out;
}

.fourth-container .inner-div {
    float: right;
    margin-right: 10px;
}

.fourth-container .inner-div ul>li {
    font-weight: 400;
    opacity: 0.4;
}

.fourth-container .experience-text p {
    text-align: center;
    color: white;
    font-weight: 600;
    margin-top: 200px;
    letter-spacing: 2px;
}

.fourth-container .inner-div .selected-year {
    font-weight: 600;
    opacity: 1;
}